import BreadCrumb from "components/atom/breadcrum";
import FiltersCustom from "components/atom/chatComponents/Filter";
import moment from "moment";
import SearchBar from "components/atom/searchBar";
import useNotification from "components/atom/snackbar";
import DeleteModal from "components/molecules/deleteModal";
import VacanciesTable from "components/molecules/vacancyTable";
import {
  disableDocViewer,
  filterByDocumentData,
  filterByDocumentType,
} from "components/utility";
import React, { useCallback, useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { useHistory, useLocation } from "react-router-dom";
import {
  changeDocumentStatus,
  deleteVacancy,
  pharmacyVacancy,
} from "redux/actions/auth";
import {
  FILTER_TYPES,
  NA,
  SNACK_MESSAGES,
  SNACK_VARIANT,
  SORT_KEY,
  STATUS_OPTIONS,
  VACANCY_TYPE,
} from "shared/constants";
import { ROUTE_CONSTANTS } from "shared/routeConstants";
import { VACANCIES_DASHBOARD_TABLE } from "shared/tableData";

function Vacancy() {
  const history = useHistory();
  const dispatch = useDispatch();
  const idParam = useLocation();
  const id = idParam?.state?.id;
  const [msg, sendNotification] = useNotification();
  const [multiplePayload, setMultiplePayload] = useState([]);
  const [userData, setUserData] = useState();
  const [activeItem, setActiveItem] = useState(1);
  const [itemsPerPage, setItemsPerPage] = useState(15);
  const [pageCount, setPageCount] = useState(1);
  const [pageItems, setPageItems] = useState(1);
  const [keyword, setKeyword] = useState("");
  const [fetchedData, setFetchedData] = useState({});
  const [sortKey, setSortKey] = useState(0);
  const [sortType, setSortType] = useState(0);
  const [multiSelection, setMultipSelection] = useState(false);
  const [toggle, setToggle] = useState({
    isActive: false,
    name: "",
  });

  const [viewDoc, setViewDoc] = useState(false);
  const [showReasonModal, setShowReasonModal] = useState(false);
  const [docViewerParams, setDocViewerParams] = useState([]);
  const [modalOpacity, setmodalOpacity] = useState(false);
  const [reasonUnverify, setReasonUnverify] = useState("");
  const [deleteModal, setDeleteModal] = useState(false);
  const [city, setCity] = useState("");
  const [country, setCountry] = useState("");
  const [priceRange, setPriceRange] = useState([0, 10]);
  const [toggleFilter, setToggleFilter] = useState(false);
  const [jobID, setJobID] = useState();
  const [dateRange, setDateRange] = useState({
    startDate: new Date(),
    endDate: new Date(),
    key: "selection",
  });

  const [filterData, setFilterData] = useState({
    country: "",
    state: "",
    selectedDate: {
      from: null,
      to: new Date(""),
    },
    rateRange: [0, 0],
  });

  useEffect(() => {
    if (!toggleFilter) {
      setCity("");
      setCountry("");
      setDateRange({
        startDate: new Date(),
        endDate: new Date(),
        key: "selection",
      });
      setPriceRange([0, 10]);
    }
  }, [toggleFilter]);

  // eslint-disable-next-line react-hooks/exhaustive-deps
  const fetchList = (isResetting = false) => {
    let payload = {
      page: pageCount,
      search: keyword,
      limit: itemsPerPage,
      sorting: sortKey,
      sort_type: sortType,
      id: id,
      // ...(country !== "" && { country: country }),
      // ...(city !== "" && { city: city }),
      // ...(priceRange !== 0 && { price: priceRange }),
      // ...(dateRange !== "" && { date: dateRange }),

      country: isResetting ? "" : filterData.country,
      state: isResetting ? "" : filterData.state,
      start_date: isResetting
        ? ""
        : filterData.selectedDate.from
        ? moment(filterData.selectedDate.from).format("YYYY-MM-DD")
        : "",
      end_date: isResetting
        ? ""
        : filterData.selectedDate.to
        ? moment(filterData.selectedDate.to).format("YYYY-MM-DD")
        : "",
      start_rate: isResetting ? "" : filterData.rateRange[0] ?? "",
      end_rate: isResetting ? "" : filterData.rateRange[1] ?? "",
    };
    dispatch(
      pharmacyVacancy(
        payload,
        (res) => {
          setFetchedData(res?.data);
          setPageItems(res.count);
        },
        (err) => {
          sendNotification({
            msg: err?.data?.message
              ? err.data.message
              : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
  };

  const handleSortIcon = () => {
    if (sortType === SORT_KEY.ascending) {
      setSortType(SORT_KEY.descending);
    } else {
      setSortType(SORT_KEY.ascending);
    }
  };

  const onDelete = () => {
    let arr = [];
    if (multiplePayload.length && !jobID) {
      arr = multiplePayload.map((i, idx) => {
        return i;
      });
    } else {
      arr = [jobID];
    }

    let data = {
      vacancy_list: arr,
    };
    dispatch(
      deleteVacancy(data, (res) => {
        fetchList();
        setDeleteModal(false);
        setMultiplePayload([]);
        sendNotification({
          msg: res?.message ? res.message : SNACK_MESSAGES.DELETED_ITEM,
          variant: SNACK_VARIANT.SUCCESS,
        });
      }),
      (err) => {
        setDeleteModal(false);
        sendNotification({
          msg: err?.data?.message
            ? err.data.message
            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
          variant: SNACK_VARIANT.ERROR,
        });
      }
    );
  };

  const viewDetails = useCallback(() => {
    let data = {
      user_id: id,
    };
    // dispatch(
    //   viewUserDetailsApi(data, (res) => {
    //     setUserData(res?.data);
    //   }),
    //   (err) => {
    //     sendNotification({
    //       msg: err?.data?.message
    //         ? err.data.message
    //         : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
    //       variant: SNACK_VARIANT.ERROR,
    //     });
    //     history.goBack();
    //   }
    // );
  }, [id, dispatch, sendNotification, history]);

  useEffect(() => {
    fetchList();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [sortKey, sortType, activeItem, itemsPerPage, pageCount]);

  const documentButtonClick = (isVerified, type, document_id) => {


    let arr = [
      {
        user_id: id,
        document_type: type ? type : docViewerParams[1],
        ...(isVerified === STATUS_OPTIONS.UNVERIFIED.key && {
          comment: reasonUnverify,
        }),
        verified: isVerified,
        document_id: document_id
          ? document_id
          : docViewerParams[0]
          ? docViewerParams[0]
          : "",
      },
    ];

    const payload = {
      doc_status_update: arr,
    };
    documentStatusChange(payload);
  };

  //Api function for document status change
  const documentStatusChange = (payload) => {
    dispatch(
      changeDocumentStatus(payload, (res) => {
        setViewDoc(false);
        setShowReasonModal(false);
        viewDetails();
        sendNotification({
          msg: res?.data?.message
            ? res?.data.message
            : SNACK_MESSAGES.SAVED_SUCCESSFULLY,
          variant: SNACK_VARIANT.SUCCESS,
        });
      }),
      (err) => {
        setShowReasonModal(false);
        sendNotification({
          msg: err?.data?.message
            ? err.data.message
            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
          variant: SNACK_VARIANT.ERROR,
        });
      }
    );
  };

  const onDocumentView = (id, type, docs) => {
    if (disableDocViewer(docs, type)) {
      setViewDoc(true);
      setDocViewerParams([
        id,
        type,
        filterByDocumentType(docs, type),
        filterByDocumentData(docs, type),
      ]);
    } else {
      sendNotification({
        msg: SNACK_MESSAGES.DOCUMENT_NOT_UPLOADED,
        variant: SNACK_VARIANT.ERROR,
      });
    }
  };

  const handleChangeFilterData = (key, value) => {
    setFilterData({ ...filterData, [key]: value });
  };

  const handleFetchDataForFilter = () => {
    fetchList();
  };

  const resetFilter = () => {
    fetchList({ isResetting: true });
    setFilterData({
      country: "",
      state: "",
      selectedDate: {
        from: null,
        to: new Date(""),
      },
      rateRange: [0, 0],
    });
  };

  return (
    <div>
      {" "}
      <div className="d-flex flex-wrap align-items-center justify-content-between">
        <BreadCrumb
          onClick={() => history.push(ROUTE_CONSTANTS.DASHBOARD)}
          previousLink={"Dashboard"}
          currentLink={"Vacancies"}
        />
        <div className="d-flex admin_action flex-wrap">
          {multiplePayload.length ? (
            <button
              className={`btn btn-danger`}
              onClick={() => setDeleteModal(true)}
            >
              Delete
            </button>
          ) : null}

          <SearchBar
            searchAPI={() => fetchList()}
            keyword={keyword}
            setKeyword={setKeyword}
            setPageCount={setPageCount}
            filter={true}
            pageCount={pageCount}
            toggle={toggleFilter}
            setToggle={setToggleFilter}
          />
        </div>
      </div>
      <FiltersCustom
        toggle={toggleFilter}
        filterData={filterData}
        reset={resetFilter}
        handleChangeFilterData={handleChangeFilterData}
        handleFetchDataForFilter={handleFetchDataForFilter}
      />
      <hr className="divider" />
      <VacanciesTable
        multiplepayload={multiplePayload}
        setmultiplePayload={setMultiplePayload}
        navigate={(id) =>
          history.push(ROUTE_CONSTANTS.VACANCIES_VIEW, {
            id: id,
            type: VACANCY_TYPE.DASHBOARD,
          })
        }
        data={fetchedData}
        onDelete={() => setDeleteModal(true)}
        itemsPerPage={itemsPerPage}
        setItemsPerPage={setItemsPerPage}
        pageCount={pageCount}
        setPageCount={setPageCount}
        sortKey={sortKey}
        setSortKey={setSortKey}
        handleSort={handleSortIcon}
        sortType={sortType}
        pageItems={pageItems}
        setPageItems={setPageItems}
        multiSelection={multiSelection}
        setMultipSelection={setMultipSelection}
        setToggle={setToggle}
        toggle={toggle}
        type={VACANCY_TYPE.DASHBOARD}
        searchAPI={() => fetchList()}
        keyword={keyword}
        setKeyword={setKeyword}
        setId={setJobID}
      />
      <DeleteModal
        show={deleteModal}
        setShow={setDeleteModal}
        onClick={onDelete}
      />
    </div>
  );
}

export default Vacancy;
