// libs
import React from "react";
import moment from "moment";
import { Virtuoso } from "react-virtuoso";

// utils
import { getDocsUrl } from "../../utilities";

const getLastMessageDetails = (lastMessages = [], user = {}) => {
    const latestUserData =
        lastMessages.filter((message = {}) => message.id == user?.id)?.[0] || user || {};

    return (
        <>
            <span className="msg_time">{showLastSeen(latestUserData)}</span>
            <p>
                {latestUserData?.last_msg?.length > 20
                    ? latestUserData?.last_msg?.slice(0, 23) + "..."
                    : latestUserData?.last_msg}
            </p>
        </>
    );
};

const showLastSeen = (user) => {
    return user?.is_online
        ? "Active now"
        : user?.last_msg_time
        ? moment(user?.last_msg_time).format("h:mm a")
        : "Active long ago";
};

const UsersListWithInfiniteScroll = ({
    styles = {},
    data = {},
    endReached = () => {},
    overscan = 10,
    itemClick = () => {},
    isChatList = false,
    activeUser = {},
    lastMessages = [],
}) => {
    return (
        <React.Fragment>
            <Virtuoso
                style={styles}
                data={data}
                endReached={endReached}
                overscan={overscan}
                listClassName="chat_scroll"
                itemContent={(index, user = {}) => {
                    return (
                        <li
                            key={user.id + index}
                            onClick={() => itemClick(user)}
                            className={activeUser?.id == user?.id ? "active" : ``}
                        >
                            <div className="d-flex chat_items">
                                <figure className="avtar avtar50 mb-0">
                                    <img
                                        src={getDocsUrl(user?.photo)}
                                        alt="Locumbay"
                                        height="50"
                                        width="50"
                                    />
                                </figure>
                                <div className="chat_summary">
                                    <h6 className="h6 mb-0 text-capitalize">{`${user?.first_name} ${user?.last_name}`}</h6>
                                    {/* {isChatList && ( */}
                                        <>
                                            {/* {user?.unread_msg_count ? (
                                                <small className="unread_msg">
                                                    {user?.unread_msg_count}
                                                </small>
                                            ) : null} */}
                                            {/* <span className="msg_time">{showLastSeen(user)}</span> */}
                                        </>
                                    {/* // )} */}
                                    {isChatList ? 
                                        getLastMessageDetails(lastMessages, user)
                                    : null}
                                </div>
                            </div>
                        </li>
                    );
                }}
                components={() => "Loading"}
            />
        </React.Fragment>
    );
};

export default UsersListWithInfiniteScroll;
