import BreadCrumb from "components/atom/breadcrum";
import useNotification from "components/atom/snackbar";
import {
  convertTimeToAmPm,
  getDatefromDayOfWeek,
  timeAgo,
} from "components/utility";
import React, { useCallback, useEffect } from "react";
import { useState } from "react";
import { useDispatch } from "react-redux";
import { useHistory, useLocation } from "react-router-dom";
import {
  DAY_NAMES,
  NA,
  PHARMACY_USER_VIEW,
  SNACK_MESSAGES,
  SNACK_VARIANT,
  USERS_CONSTS,
  VACANCY_TYPE,
} from "shared/constants";
import { ROUTE_CONSTANTS } from "shared/routeConstants";
import { Images } from "shared/images";
import GoogleMap from "components/molecules/googleMap";
import Ratingstars from "components/atom/rating";
import { deleteVacancy, jobView } from "redux/actions/auth";
import "./style.scss";
import DeleteModal from "components/molecules/deleteModal";

function VacanciesView() {
  const history = useHistory();
  const dispatch = useDispatch();
  const idParam = useLocation();
  const id = idParam?.state?.id;
  const type = idParam?.state?.type;
  const [viewData, setViewData] = useState({});
  const [data, setData] = useState({});
  const [deleteModal, setDeleteModal] = useState(false);
  const [msg, sendNotification] = useNotification();

  const ratingData = [
    {
      title: PHARMACY_USER_VIEW.JOB_VIEW.STAFF_LEVELS,
      rating: 2,
    },
    {
      title: PHARMACY_USER_VIEW.JOB_VIEW.KNOWLEDGE,
      rating: 4,
    },
    {
      title: PHARMACY_USER_VIEW.JOB_VIEW.SUPPORT,
      rating: 2,
    },
    {
      title: PHARMACY_USER_VIEW.JOB_VIEW.ACCESSIBILITY,
      rating: 3,
    },
    {
      title: PHARMACY_USER_VIEW.JOB_VIEW.PAYMENT,
      rating: 5,
    },
    {
      title: PHARMACY_USER_VIEW.JOB_VIEW.OVERALL,
      rating: 4,
    },
  ];

  const viewUser = useCallback(() => {
    // Creating an object with the id of the current invoice to be retrieved.
    let data = { id: id };
    // Dispatching an API call to retrieve the invoice details from the database.
    dispatch(
      jobView(data, (res) => {
        // If the retrieval is successful, updating the state variable with the invoice details.
        setViewData(res?.data);
      }),
      (err) => {
        // If the retrieval is unsuccessful, displaying an error notification message and navigating back to the previous page.
        sendNotification({
          msg: err?.data?.message
            ? err.data.message
            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
          variant: SNACK_VARIANT.ERROR,
        });
        history.goBack();
      }
    );
  }, [id, dispatch, sendNotification, history]);

  // useEffect hook to retrieve the invoice details on page load.
  useEffect(() => {
    viewUser();
  }, [viewUser]);

  const onDelete = () => {
    let data = {
      vacancy_list: [id],
    };

    dispatch(
      deleteVacancy(data, (res) => {
        setDeleteModal(false);
        history.push(ROUTE_CONSTANTS.VACANCIES);
        sendNotification({
          msg: res?.message ? res.message : SNACK_MESSAGES.DELETED_ITEM,
          variant: SNACK_VARIANT.SUCCESS,
        });
      }),
      (err) => {
        setDeleteModal(false);
        sendNotification({
          msg: err?.data?.message
            ? err.data.message
            : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
          variant: SNACK_VARIANT.ERROR,
        });
      }
    );
  };
  return (
    <>
      <div className="d-flex flex-wrap align-items-center mb-4 pb-3 border-bottom justify-content-between">
        <BreadCrumb
          onClick={() =>
            history.push(
              type === VACANCY_TYPE.DASHBOARD
                ? ROUTE_CONSTANTS.VACANCIES
                : ROUTE_CONSTANTS.PHARMACY_VIEW,
              {
                id: USERS_CONSTS.PHARMACY_USERS.ID,
              }
            )
          }
          previousLink={
            type === VACANCY_TYPE.DASHBOARD
              ? PHARMACY_USER_VIEW.VACANCIES
              : PHARMACY_USER_VIEW.PHARMACY_USERS
          }
          currentLink={PHARMACY_USER_VIEW.DETAILS}
        />

        <button className="btn btn-danger" onClick={() => setDeleteModal(true)}>
          Delete
        </button>
      </div>
      <div className="row DetailRow">
        <div className="col-xxl-9 col-xl-8 col-lg-8 col-md-12 col-12 left_card">
          <div className="DetailRow_card ">
            <div className="card mb-4 DetailRow_card_block">
              <div className="card-body">
                <>
                  <div className="d-flex flex-md-row flex-column align-items-start align-items-md-center mb-1 job_id">
                    <h5 className="h5 mb-0">
                      {PHARMACY_USER_VIEW.JOB_VIEW.JOB_ID}
                      {` - #${viewData?.id} -  `}
                    </h5>
                    <span>{`${PHARMACY_USER_VIEW.JOB_VIEW.POSTED} ${timeAgo(
                      viewData?.created_at
                    )}`}</span>
                  </div>
                  <div className="mb-1 job_title">
                    <p>
                      {`${viewData?.job_name} Job Vacancy in ${viewData?.pharmacy_name}`}
                    </p>
                  </div>
                  <div className="job_address mb-4">
                    <span>
                      <img
                        width={21}
                        height={27}
                        style={{ fill: "Green" }}
                        src={Images.address}
                        alt="location-marker"
                      />
                      {`${viewData?.branch_address?.branch_name} ${viewData?.branch_address?.address} ${viewData?.branch_address?.city}, ${viewData?.branch_address?.state} ${viewData?.branch_address?.country} ${viewData?.branch_address?.postal_code}`}
                    </span>
                  </div>
                </>
                {viewData?.description ? (
                  <>
                    <div className="job_description">
                      <h5 className="h5">
                        {PHARMACY_USER_VIEW.JOB_VIEW.JOB_DESCRIPTION}
                      </h5>
                      <ul>
                        <span>{viewData?.description}</span>
                      </ul>
                    </div>
                  </>
                ) : null}
              </div>
            </div>
            {/* map */}
            <div className="card DetailRow_card_block mt-3">
              <div className="google_map">
                <GoogleMap
                // address={location.address}
                // lat={location.lat}
                // lng={location.lng}
                />
              </div>
            </div>
            <div className="card DetailRow_card_block mt-3">
              <div className="job_timing card-body">
                <h5 className="h5 d-flex flex-column flex-md-row align-items-md-center align-items-start">
                  Job Timing
                  {/* //<span>{`${viewData?.start_date} to ${viewData?.end_date}`}</span> */}
                </h5>
                <div className="common_collapse_card mt-3">
                  {viewData?.shift_details?.map((value, index) => {
                    return (
                      <div id={index} class="card card_light_green mb-3">
                        <div
                          class="card-header border-bottom-0"
                          id="headingOne"
                        >
                          <div className="d-flex flex-md-row flex-column align-items-md-center align-items-start">
                            <h6 class="mb-0 h6 col-md-2 shift_day">
                              {getDatefromDayOfWeek(
                                viewData?.start_date,
                                value?.day
                              )}
                            </h6>
                            <div className="d-flex flex-sm-row flex-column align-items-md-center align-items-start col-md-10  shift_time">
                              <span className="start_time me-md-5 me-3">
                                {`Time: ${
                                  value?.start_time
                                    ? convertTimeToAmPm(value?.start_time)
                                    : NA
                                } - ${
                                  value?.end_time
                                    ? convertTimeToAmPm(value?.end_time)
                                    : NA
                                }`}
                              </span>
                              <span className="end_time">
                                {`Lunch: ${
                                  value?.lunch_start_time
                                    ? convertTimeToAmPm(value?.lunch_start_time)
                                    : NA
                                } - ${
                                  value?.lunch_end_time
                                    ? convertTimeToAmPm(value?.lunch_end_time)
                                    : NA
                                }`}
                              </span>
                            </div>
                          </div>
                        </div>
                      </div>
                    );
                  })}
                </div>
              </div>
            </div>
          </div>
        </div>
        {/* left ends */}
        {/* right begins */}
        <div className="col-xxl-3 col-xl-4 col-lg-4 col-md-12 col-12 right_card mt-lg-0 mt-3">
          <div className="DetailRow_card">
            <div className="card mb-4 DetailRow_card_block">
              <div className="card-body job_body">
                {/* <div className="card card_light_green mb-3">
                  <h5 className="card-title h6">
                    {PHARMACY_USER_VIEW.JOB_VIEW.SHIFT_TIME}
                  </h5>
                  <div className="card-body">
                    <div className="table-responsive">
                      <table className="table">
                        <thead>
                          <th>
                            {PHARMACY_USER_VIEW.JOB_VIEW.START +
                              " " +
                              PHARMACY_USER_VIEW.JOB_VIEW.DATE}
                          </th>
                          <th>
                            {PHARMACY_USER_VIEW.JOB_VIEW.END +
                              " " +
                              PHARMACY_USER_VIEW.JOB_VIEW.DATE}
                          </th>
                        </thead>
                        <tbody>
                          {viewData?.shift_details?.map((value) => (
                            <tr>
                              <td>
                                {" "}
                                {value?.start_time ? value?.start_time : NA}
                              </td>
                              <td> {value?.end_time ? value?.end_time : NA}</td>
                            </tr>
                          ))}
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div> */}

                <div className="card card_light_green mb-3">
                  <h5 className="card-title h6">Rate</h5>
                  <div className="card-body ">
                    <h6>{`${viewData?.start_rate}-${viewData?.end_rate} / hr`}</h6>
                    {/* <span>Lunch break : 13:00 - 13:30</span> */}
                  </div>
                </div>

                <div className="card card_light_green">
                  <h5 className="card-title h6">
                    {PHARMACY_USER_VIEW.JOB_VIEW.PHARMACIST}
                  </h5>
                  <div className="card-body">
                    <h6>{viewData?.branch_address?.branch_name}</h6>
                    <span>{viewData?.branch_address?.postal_code}</span>
                  </div>
                </div>
              </div>
            </div>
            <div className="card DetailRow_card_block">
              <h5 className="card-title h6">
                {PHARMACY_USER_VIEW.JOB_VIEW.PROFESSIONAL_DETAILS}
              </h5>
              <div className="card-body">
                <>
                  {ratingData?.map((i, idx) => {
                    return (
                      <div className="d-flex justify-content-md-between align-items-md-center align-items-start ratingBlock">
                        <span className="h6 ratingBlock_title">{i.title} </span>
                        <span className="ratingBlock_value">
                          <Ratingstars rating={i?.rating || 3} />
                        </span>
                      </div>
                    );
                  })}
                </>
              </div>
            </div>
          </div>
        </div>
        {/* right ends */}
      </div>
      <DeleteModal
        show={deleteModal}
        setShow={setDeleteModal}
        onClick={onDelete}
      />
    </>
  );
}

export default VacanciesView;
