// libs
import React, { useState, useEffect, useContext, useCallback } from "react";
import { useHistory, useLocation } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";

// components
import Inbox from "./Inbox";
import LiveChat from "./LiveChat";

// actions
import {
  getChatUsersList,
  getUserMessagesList,
  getUsersListByName,
  uploadMedia,
} from "redux/actions/auth";

// consts
import { ROUTE_CONSTANTS } from "shared/routeConstants";
import useNotification from "components/atom/snackbar";

import { deepCopy, getQueryParamValue } from "./utilities";
import { SNACK_MESSAGES, SNACK_VARIANT } from "shared/constants";

export const MESSAGES_LIMIT = 20;
export const GET_USERS_LIMIT = 20;
export const SEARCH_USERS_LIMIT = 20;

export const CHAT_FILTER_OPTIONS = {
  INBOX: {
    ID: 0,
    TITLE: "Inbox",
  },
  DELETED: {
    ID: 1,
    TITLE: "Deleted",
  },
  ARCHIVED: {
    ID: 2,
    TITLE: "Archived",
  },
};

const Messages = () => {
  const socketManager = useSelector((state) => state.socket.socketInstance);
  //const { socket, socketError } = useContext(SocketContext);
  const location = useLocation();
  const history = useHistory();
  const dispatch = useDispatch();
  const [msg, sendNotification] = useNotification();

  // inbox state variables
  const [usersList, setUsersList] = useState({
    data: [],
    currentPage: 1,
    isLoading: false,
    totalCount: 0,
  });
  const [filteredUsersData, setFilteredUsersData] = useState({
    data: [],
    query: null,
    isLoading: false,
    currentPage: 1,
    totalCount: 0,
  });
  const [activeUser, setActiveUser] = useState(null);
  const [activeFilter, setActiveFilter] = useState(
    CHAT_FILTER_OPTIONS.INBOX.ID
  );
  const [searchSidebarVisible, setSearchSidebarVisible] = useState(false);
  const [showChatFilter, setShowChatFilter] = useState(false);
  const [hasMoreUsers, setHasMoreUsers] = useState(true);
  const [roomId, setRoomId] = useState(null);

  // active chat messages state variables
  const [newMessage, setNewMessage] = useState(null);
  const [messages, setMessages] = useState({ data: [], totalCount: 0 });
  const [sendTextValue, setSendTextValue] = useState("");
  const [attachmentDropdownVisible, setAttachmentDropdownVisible] =
    useState(false);
  const [uploadLoading, setUploadLoading] = useState(false);
  const [chatLoading, setChatLoading] = useState(false);
  const [modalStates, setModalStates] = useState({
    clear: false,
    archive: false,
    delete: false,
  });
  const [file, setFile] = useState(null);
  const [showChatOptions, setShowChatOptions] = useState(false);
  const [lastMessages, setLastMessages] = useState([]);

  // fetch Chat users List
  useEffect(() => {
    fetchChatUsers();
  }, []);

  const appendUser = useCallback(
    (newUser = {}) => {
      if (!activeUser?.isArchive) {
        const userChatExists =
          usersList?.data?.findIndex(
            (user = {}) => user?.id == (newUser?.id || activeUser?.id)
          ) !== -1;

        // return if active user already present inside usersList else unshift user to the list
        if (userChatExists) return;

        let usersCopy = deepCopy(usersList.data);
        usersCopy.unshift(
          Object.keys(newUser || {})?.length ? newUser : activeUser
        );
        setUsersList((prev) => {
          return { ...prev, data: usersCopy, totalCount: prev.totalCount + 1 };
        });
      }
    },
    [activeUser, usersList.data]
  );

  // socket events
  useEffect(() => {
    let localRoomId = null;
    if (socketManager?.socket?.connected) {
      socketManager?.socket?.on("join_room", (msg) => {
        // console.log("room joined", msg);
      });

      socketManager?.socket?.on("room_name", (msg) => {
        localRoomId = msg?.data.room_name;

        // handle unread msgs
        socketManager?.socket?.emit("msg_read", { room_name: localRoomId });
        setRoomId(localRoomId);
        // do api call for fetch messages or check if data already exist if caching
        // socketManager?.socket?.emit("getChatList");
      });

      // socketManager?.socket?.on("clear_chat", () => {
      //     // clear messages in state
      //     setMessages({ data: [], totalCount: 0 });
      //     handleToggleModal("clear", false);
      //     socketManager?.socket?.emit("chat_list", {
      //         page: 1,
      //         limit: usersList?.data?.length || GET_USERS_LIMIT,
      //     });
      //     sendNotification({
      //         msg: "Chat cleared successfully.",
      //         variant: SNACK_VARIANT.SUCCESS,
      //     });
      // });

      // archive messages
      // socketManager?.socket?.on("archive_chat", () => {
      //     removeCurrentUser();
      //     setActiveUser({});
      //     setMessages({ data: [], totalCount: 0 });
      //     handleToggleModal("archive", false);
      //     removeCurrentUser();
      //     sendNotification({
      //         msg: "Chat archived successfully.",
      //         variant: SNACK_VARIANT.SUCCESS,
      //     });
      // });

      // delete chat
      // socketManager?.socket?.on("delete_chat", () => {
      //     removeCurrentUser();
      //     setActiveUser({});
      //     setMessages({ data: [], totalCount: 0 });
      //     handleToggleModal("delete", false);
      //     sendNotification({
      //         msg: "Chat deleted successfully.",
      //         variant: SNACK_VARIANT.SUCCESS,
      //     });
      // });

      socketManager?.socket?.on("send_msg", (res) => {
        socketManager?.socket?.emit("msg_read", { room_name: localRoomId });

        setNewMessage(res?.data);
        // socketManager?.socket?.emit("chat_list", {
        //     page: 1,
        //     limit: usersList?.data?.length || GET_USERS_LIMIT,
        // });
      });

      socketManager?.socket?.on("receive_msg", (res) => {
        socketManager?.socket?.emit("msg_read", { room_name: localRoomId });

        res?.data?.from_user === activeUser?.id && setNewMessage(res?.data);
      });

      socketManager?.socket?.on("chat_list", (res) => {
        if (Array.isArray(res?.data) && res?.data?.length === 1) {
          appendUser(res?.data[0]);
        }
        setLastMessages(res?.data || []);
      });
    }

    return () => {
      if (socketManager.socket) {
        socketManager.socket.removeAllListeners();
      }
    };
  }, [activeUser?.id, appendUser, socketManager.socket]);

  // when navigation happens from different page
  useEffect(() => {
    if (socketManager.socket) {
      if (location.search) {
        const userId = getQueryParamValue(location.search, "id");
        const userData =
          usersList?.data?.filter((user) => user.id == userId)?.[0] || null;
        if (!userData) return history.push(ROUTE_CONSTANTS.MESSAGES);
        handleActiveUser(userData);
      }
    }
  }, [location, usersList, socketManager.socket]);

  /* change users list order if new message is updated in state & 
  append sent/received message in messages list */
  useEffect(() => {
    if (newMessage) {
      sortChatList();
      appendMessage();
      activeUser?.id && appendUser();
      emitUpdatedChatList();
    }
  }, [newMessage]);

  // fetch active User Chat
  useEffect(() => {
    if (roomId) {
      fetchUserMessages();
    }
  }, [roomId]);

  // upload media to get the asset url
  useEffect(() => {
    if (file) {
      uploadMediaCall(file);
    }
  }, [file]);

  const sortChatList = () => {
    let chatList = deepCopy(usersList?.data);
    let prevUserIndex = chatList.findIndex(
      (user) => user?.id === activeUser?.id
    );
    if (prevUserIndex > 0) {
      let user = chatList[prevUserIndex];
      chatList.splice(prevUserIndex, 1);
      chatList.unshift({ ...user, last_msg: "", last_msg_time: "" });
      setUsersList((prev) => {
        return { ...prev, data: chatList };
      });
    }
  };

  const removeCurrentUser = () => {
    if (activeUser?.id) {
      setUsersList((prev) => {
        return {
          ...prev,
          data:
            prev?.data?.filter((user = {}) => user?.id !== activeUser?.id) ||
            [],
          totalCount: prev.totalCount - 1,
        };
      });
    }
  };

  const appendMessage = () => {
    let messagesCopy = deepCopy(messages.data);
    messagesCopy.push(newMessage);
    setMessages((prev) => {
      return { ...prev, data: messagesCopy };
    });
    setNewMessage(null);
  };

  // filter users on the basis of name
  const handleSearchChange = ({ target: { value: wordToMatch } }) => {
    if (wordToMatch.slice(-1) !== "\\") {
      const regex = new RegExp(wordToMatch, "gi");
      let filteredData = usersList?.data?.filter((user) => {
        let userName = user.first_name + user.last_name;
        return userName.match(regex);
      });

      setFilteredUsersData((prev) => {
        return {
          ...prev,
          query: wordToMatch?.replace(/[^a-zA-Z0-9 ]/g, ""),
          data: filteredData,
          currentPage: 1,
        };
      });
    }
  };

  // fetch chat history of user
  const fetchUserMessages = useCallback(
    (messageId = "") => {
      const payload = {
        limit: MESSAGES_LIMIT,
        room_name: roomId,
        id: messageId,
        isDebounceCall: true,
      };
      dispatch(
        getUserMessagesList(
          payload,
          (res = {}, type) => {
            setMessages((prev) => {
              return {
                data: !messageId
                  ? res?.data || []
                  : [...res?.data, ...prev.data],
                totalCount: res?.count || 0,
              };
            });
            setChatLoading(false);
          },
          (err) => {
            setMessages({ data: [], totalCount: 0 });
            setChatLoading(false);
            sendNotification({
              msg: err || SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
              variant: SNACK_VARIANT.ERROR,
            });
          }
        )
      );
    },
    [roomId, messages]
  );

  // fetch chat users
  const fetchChatUsers = useCallback(
    (disabledLoading = false, initialPagefetch = 0, isArchive = 0) => {
      const payload = {
        page: initialPagefetch || usersList?.currentPage || 1,
        limit: GET_USERS_LIMIT,
        archive: isArchive,
        isDebounceCall: disabledLoading,
      };
      dispatch(
        getChatUsersList(
          payload,
          (res = {}, type) => {
            // append latest data with prev one and increment currentPage
            setUsersList((prev) => {
              const updatedData =
                isArchive || initialPagefetch == 1
                  ? res?.data
                  : res?.data || []
                  ? [...prev.data, ...res.data]
                  : [...prev?.data];
              return {
                ...prev,
                data: updatedData,
                currentPage: prev.currentPage + 1,
                totalCount: res?.count,
              };
            });
          },
          (err) => {
            setUsersList((prev) => {
              return {
                ...prev,
                currentPage: 1,
                data: [],
                isLoading: false,
                totalCount: 0,
              };
            });
            sendNotification({
              msg: err || SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
              variant: SNACK_VARIANT.ERROR,
            });
          }
        )
      );
    },
    [usersList]
  );

  // search users on server
  const fetchUsersByName = useCallback(
    (searchCall = false) => {
      const payload = {
        search: filteredUsersData?.query,
        page: searchCall ? 1 : filteredUsersData?.currentPage || 1,
        limit: SEARCH_USERS_LIMIT,
        isDebounceCall: true,
      };
      if (filteredUsersData?.query !== null) {
        // show loading when search call happens
        if (!filteredUsersData?.query?.trim()?.length) return;
        if (searchCall) {
          setFilteredUsersData((prev) => {
            return {
              ...prev,
              isLoading: true,
            };
          });
        }
        dispatch(
          getUsersListByName(
            payload,
            (res = {}, type) => {
              // apend searched data with new one and increment current page
              if (searchCall) {
                setFilteredUsersData((prev) => {
                  return {
                    ...prev,
                    isLoading: false,
                    data: res?.data ? res.data : [],
                    currentPage: prev.currentPage + 1,
                    totalCount: res?.count,
                  };
                });
              } else {
                setFilteredUsersData((prev) => {
                  return {
                    ...prev,
                    isLoading: false,
                    data: res?.data
                      ? [...prev.data, ...res.data]
                      : [...prev?.data],
                    currentPage: prev.currentPage + 1,
                  };
                });
              }
            },
            (err) => {
              setFilteredUsersData((prev) => {
                return {
                  ...prev,
                  isLoading: false,
                  data: [],
                  currentPage: 1,
                  totalCount: 0,
                };
              });
              sendNotification({
                msg: err || SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
                variant: SNACK_VARIANT.ERROR,
              });
            }
          )
        );
      }
    },
    [filteredUsersData]
  );

  // upload Media
  const uploadMediaCall = useCallback(() => {
    let type = "";
    if (file?.type?.indexOf("video") !== -1) {
      type = "2";
    } else if (file?.type?.indexOf("audio" || "mpeg") !== -1) {
      type = "3";
    } else if (file?.type?.indexOf("application") !== -1) {
      type = "4";
    } else {
      type = "1";
    }

    const payload = {
      type,
      media: file,
      room_name: roomId,
      isDebounceCall: true,
    };
    setUploadLoading(true);
    dispatch(
      uploadMedia(
        payload,
        (res = {}) => {
          if (res.data) {
            sendMessage(type, res.data);
          }
          setUploadLoading(false);
        },
        (err) => {
          setFile(null);
          setUploadLoading(false);
          sendNotification({
            msg: err || SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            variant: SNACK_VARIANT.ERROR,
          });
        }
      )
    );
  }, [roomId, file]);

  const handleActiveUser = (user) => {
    if (activeUser?.id !== user?.id && socketManager.socket) {
      // triggerEvent join event to create roomId
      socketManager.socket?.emit("join", { to_user: user?.id });

      setActiveUser(user);
      setChatLoading(true);

      // clear send message input active user gets changed
      setSendTextValue("");
      setMessages({ data: [], count: 0 });
    }
  };

  const handleActiveFilter = (filter) => {
    // archive
    if (filter == 2) {
      fetchChatUsers(false, 1, 1);
    } else {
      fetchChatUsers(false, 1);
    }
    setActiveFilter(filter);
  };

  const handleSearchSidebar = (state) => {
    setSearchSidebarVisible(state);
    setFilteredUsersData((prev) => {
      return {
        ...prev,
        data: usersList?.data || [],
        query: null,
        currentPage: 1,
        totalCount: 0,
      };
    });
  };

  const handleSendMessage = (e) => {
    e.preventDefault();
    sendTextValue?.length && sendMessage("0");
  };

  const handleKeypress = (e) => {
    if (e.key == "Enter" && !e.shiftKey) {
      e.preventDefault();
      handleSendMessage(e);
    }
  };

  const handleSendTextChange = (e) => {
    if (e.charCode === 13 && e.key === "shift") {
      sendMessage("0");
    } else if (e.charCode === 13 && !e.shiftKey) {
      e.preventDefault();
      setSendTextValue(e.target.value, "\r", "\n");
    } else {
      setSendTextValue(e.target.value);
    }
  };

  const sendMessage = (type = "0", value = "") => {
    if (sendTextValue.trim().length && type == "0") {
      socketManager.socket?.emit("send_msg", {
        room_name: roomId,
        message: value || sendTextValue.trim(),
        timestamp: new Date(),
        type,
      });
      setSendTextValue("");
    } else {
      socketManager.socket?.emit("send_msg", {
        room_name: roomId,
        type,
        url: value,
      });
      setFile(null);
    }
  };

  const handleUploadMedia = (e, type) => {
    if (e.target?.files[0]?.type) {
      if (e.target?.files[0]?.type.includes(type)) {
        if (e.target?.files[0]?.size) {
          setFile(e.target.files[0]);
        }
        e.target.value = null;
        setAttachmentDropdownVisible(false);
      } else {
        sendNotification({
          msg: `Please Select ${type} file`,
          variant: SNACK_VARIANT.ERROR,
        });
      }
    }
  };

  const handleToggleModal = (type, value) => {
    setModalStates((prev) => {
      return {
        ...prev,
        [type]: value,
      };
    });
  };

  const handleClearChat = () => {
    socketManager.socket?.emit(
      "clear_chat",
      {
        room_name: roomId,
      },
      () => {
        setMessages({ data: [], totalCount: 0 });
        handleToggleModal("clear", false);
        emitUpdatedChatList();

        sendNotification({
          msg: "Chat cleared successfully.",
          variant: SNACK_VARIANT.SUCCESS,
        });
      }
    );
  };

  const emitUpdatedChatList = () => {
    // trigger chat list to fetch inbox/archived latest chat data
    socketManager.socket?.emit("chat_list", {
      page: 1,
      limit: usersList?.data?.length || GET_USERS_LIMIT,
      archive: activeFilter == 2 ? 1 : 0,
    });
  };

  const handleArchiveChat = (type = "") => {
    if (type == "unarchive") {
      socketManager.socket?.emit(
        "unarchive_chat",
        {
          room_name: roomId,
        },
        () => {
          removeCurrentUser();
          setActiveUser({});
          setMessages({ data: [], totalCount: 0 });
          handleToggleModal("archive", false);
          sendNotification({
            msg: "Chat un-archived successfully.",
            variant: SNACK_VARIANT.SUCCESS,
          });
        }
      );
    }

    if (activeFilter !== 2) {
      socketManager.socket?.emit(
        "archive_chat",
        {
          room_name: roomId,
        },
        () => {
          removeCurrentUser();
          setActiveUser({});
          setMessages({ data: [], totalCount: 0 });
          handleToggleModal("archive", false);
          sendNotification({
            msg: "Chat archived successfully.",
            variant: SNACK_VARIANT.SUCCESS,
          });
        }
      );
    }
  };

  const handleDeleteChat = () => {
    socketManager.socket?.emit(
      "delete_chat",
      {
        room_name: roomId,
      },
      () => {
        removeCurrentUser();

        setActiveUser({});
        setMessages({ data: [], totalCount: 0 });
        handleToggleModal("delete", false);
        sendNotification({
          msg: "Chat deleted successfully.",
          variant: SNACK_VARIANT.SUCCESS,
        });
      }
    );
  };

  return (
    <div className="form_white chat_panel">
      <div className="row">
        <aside className="col-xl-4 col-xxl-3 col-md-4 chat_leftBar">
          <Inbox
            activeUser={activeUser}
            handleActiveUser={handleActiveUser}
            setMessages={setMessages}
            usersList={usersList?.data || []}
            totalUsers={usersList?.totalCount}
            history={history}
            handleActiveFilter={handleActiveFilter}
            activeFilter={activeFilter}
            handleFilterUsersByName={handleSearchChange}
            searchKeyword={filteredUsersData?.query}
            isLoading={filteredUsersData?.isLoading}
            searchedData={filteredUsersData?.data || []}
            setSearchSidebarVisible={handleSearchSidebar}
            searchSidebarVisible={searchSidebarVisible}
            showChatFilter={showChatFilter}
            setShowChatFilter={setShowChatFilter}
            fetchUsersByName={fetchUsersByName}
            fetchChatUsers={fetchChatUsers}
            totalSearchUsers={filteredUsersData?.totalCount}
            lastMessages={lastMessages}
          />
        </aside>
        <aside className="col-xl-8 col-xxl-9 col-md-8 chat_rightBar">
          <LiveChat
            activeUser={activeUser}
            chatLoading={chatLoading}
            uploadLoading={uploadLoading}
            handleSendMessage={handleSendMessage}
            handleKeypress={handleKeypress}
            messages={messages}
            setAttachmentDropdownVisible={setAttachmentDropdownVisible}
            attachmentDropdownVisible={attachmentDropdownVisible}
            onChangeFile={handleUploadMedia}
            sendTextValue={sendTextValue}
            handleSendTextChange={handleSendTextChange}
            handleToggleModal={handleToggleModal}
            modalStates={modalStates}
            handleClearChat={handleClearChat}
            handleArchiveChat={handleArchiveChat}
            handleDeleteChat={handleDeleteChat}
            fetchUserMessages={fetchUserMessages}
            setShowChatOptions={setShowChatOptions}
            showChatOptions={showChatOptions}
            activeFilter={activeFilter}
          />
        </aside>
      </div>
    </div>
  );
};

export default Messages;
