// libs
import React from "react";

// components
import CustomDropdownList from "components/atom/CustomDropdownList";
import UsersListWithInfiniteScroll from "./UsersListWithInfiniteScroll";

// consts
import { CHAT_FILTER_OPTIONS } from "../..";

const ChatList = ({
    setSearchSidebarVisible = () => {},
    handleActiveFilter = () => {},
    usersList = [],
    showChatFilter = false,
    setShowChatFilter = () => {},
    activeFilter,
    handleActiveUser = () => {},
    activeUser = {},
    lastMessages = [],
    totalUsers = 0,
    fetchChatUsers = () => {}
}) => {

    const fetchMoreUsers = (idx) => {
        if (usersList.length < totalUsers) {
            fetchChatUsers(idx);
        }
    };

    return (
        <>
            {/* do stylings like search */}
            <div className="form-control" onClick={() => setSearchSidebarVisible(true)}>
                Search people
            </div>

            <div className="d-flex justify-content-between chat_title">
                <h6 className="mb-0">{activeFilter == 2 ? "Archived" : "Inbox"}</h6>
                <CustomDropdownList
                    isVisible={showChatFilter}
                    handleIsVisible={setShowChatFilter}
                    activeIndex={activeFilter}
                    dropdownOptions={[
                        {
                            title: CHAT_FILTER_OPTIONS.INBOX.TITLE,
                            onClick: () => {
                                handleActiveFilter(CHAT_FILTER_OPTIONS.INBOX.ID);
                            },
                            id: CHAT_FILTER_OPTIONS.INBOX.ID, // for display active filter and detect which filter is applied
                        },
                        // {
                        //     title: CHAT_FILTER_OPTIONS.DELETED.TITLE,
                        //     onClick: () => {
                        //         handleActiveFilter(CHAT_FILTER_OPTIONS.DELETED.ID);
                        //     },
                        //     id: CHAT_FILTER_OPTIONS.DELETED.ID,
                        // },
                        {
                            title: CHAT_FILTER_OPTIONS.ARCHIVED.TITLE,
                            onClick: () => {
                                handleActiveFilter(CHAT_FILTER_OPTIONS.ARCHIVED.ID);
                            },
                            id: CHAT_FILTER_OPTIONS.ARCHIVED.ID,
                        },
                    ]}
                    activeClassName={"active"}
                    className=""
                    // heading={"Filter"}
                    headingComponent={() => <svg width="21" height="21" viewBox="0 0 21 21" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M0.260035 1.93991C-0.356309 1.15816 0.192251 0 1.17923 0H19.8209C20.8078 0 21.3564 1.15816 20.7399 1.93991L13.8854 10.6319C13.2176 11.4788 12.8539 12.5308 12.8539 13.6154V15.9318C12.8539 17.1985 12.358 18.4132 11.4748 19.3091L10.1551 20.6479C9.41364 21.4001 8.1459 20.8672 8.1459 19.8036V13.6154C8.1459 12.5309 7.78217 11.4788 7.11422 10.6319L0.260035 1.93991Z" fill="#104651"/>
                    </svg> }
                    icon
                />
            </div>

            <ul className="chat_list list-unstyled">
                {usersList?.length > 0 ? (
                    <UsersListWithInfiniteScroll
                        // styles={{ height: 400 }}
                        endReached={fetchMoreUsers}
                        overscan={20}
                        itemClick={handleActiveUser}
                        activeUser={activeUser}
                        lastMessages={lastMessages}
                        isChatList={true}
                        data={usersList}
                    />
                ) : (
                    <div className="d-flex align-items-center justify-content-center no_users"><span>No Users</span></div>
                )}
            </ul>
        </>
    );
};

export default ChatList;
